@use '~ui/library';

.button {
    width: library.$x * 42/4;
    height: library.$x * 42/4;
    border-radius: library.$border-radius;
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    transition: background .25s;
    transform-origin: center;
    &:hover {
        background: library.$c4;
    }
}

.icon {
    width: library.$x * 7;
    height: library.$x * 7;
}
