@use '~ui/library';

.container {
    height: 100%;
    background: library.$c1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 library.$x * 20;
    gap: library.$x * 2;
}

.content-common {
    display: flex;    
    align-items: center;
}

.leftControls{
    @extend .content-common;
    height: 100%;    
    gap: library.$x * 5;
    a {
        @extend .content-common;
    }
}

.buttons{
    @extend .content-common;
    gap: library.$x * 2;  
}

.profile {
    @extend .content-common;    
    gap: library.$x * 5;
    border-radius: library.$border-radius;
    transition: background .25s;
    &:hover {
        background: library.$c4;
    }
}

.userPicture {
    width: library.$x * 42/4;
    height: library.$x * 42/4;
    object-fit: cover;
    border-radius: 50%;
}

.username {
    @include library.hideRestOfText;
    @include library.t2;
    min-width: library.$x * 25;
    max-width: library.$x * 40;
}

