@use '~ui/library';

.container a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin: auto;
    aspect-ratio: 3/1;
    position: relative;
    overflow: hidden;
    border-radius: library.$border-radius;
    background: library.$c1;
    transition: box-shadow .25s;
    &:hover {
        box-shadow: 0 library.$x library.$x * 4 library.$c0p25;
    }
}

.text {
    display: flex;
    flex-direction: column;
    gap: library.$x * 2;
    align-items: center;    
}

.header {
    @include library.t1;
    color: library.$c13;
}

.description {
    @include library.t2;
    color: library.$c12;
    opacity: 0.5;
    & p {
        text-align: center;
        margin-top: library.$x;
    }
}

.girl {
    @include library.asset;
    left: 0;
    bottom: 0;
    height: 80%;
}

.guy {
    @include library.asset;
    right: library.$x * 2;
    bottom: 0;
    height: 80%;
}

