@use '~ui/library';

.container {
    aspect-ratio: 16/9;
    position: relative;    
    &:hover .pic {
        box-shadow: 0 library.$x * 2 library.$x * 2 library.$c0p25;
    }
}

.pic {
    border-radius: library.$border-radius;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: box-shadow .25s;
}

.name {
    @include library.hideRestOfText;
    @include library.t1;
    background: library.$c1;
    position: absolute;
    bottom: 0%;
    left: 50%;   
    width: 50%;
    min-height: library.$x * 8;
    padding: library.$x * 0.5 library.$x;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: library.$border-radius;
    box-shadow: 0 1.5 * library.$x library.$c0p25;
    z-index: 1;
} 