@use '~ui/library';

.menu {
    width: library.$x * 10;
    height: library.$x * 10;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
        cursor: pointer;       
    }
}

.icon {
    width: library.$x * 5;
    height: library.$x * 5;
    & path {
        stroke: library.$c10;
    }
    
}