@use "sass:map";
@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

$x: (4 * 100 / 1080) * 1vb;

$c0p25: rgba(#000, .25);
$c0p5: rgba(#000, .5);
$c10: #9A8ADB;
$c11: #8779BF;
$c12: #FFF6F0;
$c13: #FFB55F;
$c1: #403B4A;
$c2: #464152;
$c2p5: #4C4759;
$c3: #615B73;
$c4: #575366;
$c4a85: rgba($c4, .85);

$status: (
    "online": #3EA071,
    "deserted": #F9C339,
    "talking": #F93939,
    "offline": #2A233F,
);

$border-radius: 5px;

@mixin button {
    cursor: pointer;
}

@mixin hideRestOfText {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@mixin defaultFont {
    font-family: 'Jost' sans-serif;
}

@mixin w1 {
    font-weight: 300;
}

@mixin w2 {
    font-weight: 400;
}

@mixin w3 {
    font-weight: 600;
}

@mixin H1 {
    @include defaultFont;
    font-size: 60 * $x;
}

@mixin H2 {
    @include defaultFont;
    font-size: 16 * $x;
}

@mixin H3 {
    @include defaultFont;
    font-size: 7.75 * $x;
}

@mixin t1 {
    @include defaultFont;
    font-size: 6.5 * $x;
}

@mixin t2 {
    @include defaultFont;
    font-size: 5 * $x;
}

@mixin t3 {
    @include defaultFont;
    font-size: 4 * $x;
}

@mixin asset {
    position: absolute;
    pointer-events: none;
}