@use '~ui/library';

.section {
    margin-top: library.$x * 8;
    padding-top: library.$x * 4;
    width: library.$x * 100;
    height: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: library.$x * 8;
    &::-webkit-scrollbar {
        width: 0px;
    }
}