@use '~ui/library';

.authForm {
    width: 100%;
    min-width: fit-content;
    @include library.w1;
    display: flex;
    flex-direction: column;
    gap: library.$x * 6;
    a {
        &:hover, &:focus {
            color: library.$c13;
            transition: all 0.2s;
        }
    } 
} 

.formController {
    width: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: library.$x * 6;
    align-self: center;
}