@use '~ui/library';

.authPage {
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: center;
}

.appName {
    @include library.asset;
    width: 100%;    
    top: 100%;
    left: 50%;
    transform: translate(-50%, -88%);
    z-index: 1;
}
 
.appNameBackground {
    position: relative;
    width: 100%;
    height: library.$x * 120;
    background: library.$c11;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
}

.notes {
    @include library.asset;
    top: 0;
    left: 0;
}

.notesWrapper {
    position: absolute;
    left:0;
    bottom: 0;
    overflow: hidden;
    width: 100%;
    z-index: -1;
}

.fakeNotes {
    visibility: hidden; 
}

.contentContainer {
    display: flex;
    flex: 1;
    position: relative;   
}

.conent {
    @include library.H3;
    width: library.$x * 150;
    margin: library.$x * 6 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: library.$c12;
}

.authPageFooter {
    color: library.$c2p5;
    margin: library.$x * 6 auto;
}

@mixin peopleContainer {
    display: flex;
    align-items: end;
    z-index: 1;
}

.manContainer {
    @include library.asset;
    @include peopleContainer;
    bottom: 0;
    left: library.$x * -200;
}

.womanContainer {
    @include library.asset;
    @include peopleContainer;
    bottom: 0;
    right: library.$x * -200;
}

.person {
    height: library.$x * 170;
}

.micro {
    height: library.$x * 100;
}