@use '~ui/library';

.container {
    display: flex;
    align-items: center;
    width: 100%;
    gap: library.$x * 2;
    padding: library.$x;
    transition: box-shadow .25s, background .25s;
    border-radius: library.$border-radius;
    &:hover {
        background: library.$c4;
        box-shadow: 0 library.$x library.$x * 2 library.$c0p25;
    }
} 

.name {
    @include library.hideRestOfText;
    @include library.t2;
    flex: 1;
}

.pic {
    width: library.$x * 12;
    height: library.$x * 12;
    object-fit: cover;
    border-radius: 50%;
}