@use '~ui/library';

.container {
    @include library.t2;
    width: 90%;
    margin: 0 auto;
    box-shadow: 0 0 library.$x * 6 library.$c0p25;
    aspect-ratio: 8/1;
    min-height: library.$x * 12;
    background:library.$c2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: library.$x * 2;
}

.name {
    @include library.t2;
    flex: 1;
    padding: 0 library.$x*2; 
}
